<template>
  <div class="container wrapper">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="">
        <el-date-picker
          v-model="form.day"
          type="date"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.name" placeholder="5"></el-input>
      </el-form-item>
      <el-card v-for="(item, index) in link" :key="`preview-${index}`" style="margin-bottom: 10px;">
        <el-form-item :label="`${index + 1}`">
          <iframe :src="`${item}?day=${form.day}&name=${form.name}`"></iframe>
        </el-form-item>
        <el-form-item label="">
          <el-link
            type="primary"
            icon="el-icon-document-copy"
            v-clipboard:copy="`${item}?day=${form.day}&name=${form.name}`"
            v-clipboard:success="handleCopy"
            ></el-link
          >
        </el-form-item>
      </el-card>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        day: "2021/12/31",
        name: "2021",
      },
      link: [
        "https://httishere.github.io/notion-widget/days-matter2.html",
        "https://httishere.github.io/notion-widget/days-matter.html",
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now();
        },
      },
    };
  },
  methods: {
    handleCopy() {
      this.$message({
        message: "Notion",
        type: "success",
      });
    },
  },
};
</script>
<style lang="less" scoped>
iframe {
  width: 320px;
  height: 320px;
  outline: none;
  border: none;
}
</style>
